export default {
	props:{
		item:{
			type:Object,
			default(){
				return null
			}
		}
	},
	data() {
		return {
			
		}
	},
	methods: {
		handleGuanzhu(){
			this.item.isGuanzhu=true
			uni.showToast({
				title:"关注成功",
				duration:1000
			})
		},
		handleCaozuo(type){
			let info=this.item.infoNum
			switch (type){
				case 'ding':
					if (info.index==1) {return}
					info.dingNum++
					if(info.index==2){
						info.caiNum--
					}
					info.index=1
					break;
				case 'cai':
					if (info.index==2) {return}
					info.caiNum++
					if(info.index==1){
						info.dingNum--
					}
					info.index=2
					break;
				default:
					break;
			}
		},
		openDetail(){
			console.log("进入详情页")
		}
	}
}
